label .menu {
    position: absolute;
    right: calc(50% - 50px);
    top: -100px;
    z-index: 100;
    width: 100px;
    height: 100px;
    background: #FFF;
    border-radius: 50% 50% 50% 50%;
    transition: .5s ease-in-out;
    box-shadow: 0 0 0 0 #FFF, 0 0 0 0 #FFF;
    cursor: pointer;
    
  }
  
  label .hamburger {
    position: absolute;
    top: 135px;
    left: 50px;
    width: 30px;
    height: 5px;
    border-radius: 5px;
    background: #444440;
    display: block;
    -webkit-transform-origin: center;
    transform-origin: center;
    transition: .5s ease-in-out;
  }
  
  label .hamburger:after, label .hamburger:before {
    transition: .5s ease-in-out;
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background: #444440;
  }
  
  label .hamburger:before { top: -10px; }
  
  label .hamburger:after { bottom: -10px; }
  
  label input { display: none; }
  
  label input:checked + .menu {
    box-shadow: 0 0 0 100vw #FFF, 0 0 0 100vh #FFF;
    border-radius: 0;
  }
  
  label input:checked + .menu .hamburger {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    
  }
  
  label input:checked + .menu .hamburger:after {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    bottom: 0;
    
  }
  
  label input:checked + .menu .hamburger:before {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    top: 0;
    
  }
  
  label input:checked + .menu + ul { display: block; /*show menu when active*/ }
  
  label ul {
    list-style: none;
    z-index: 200;
    position: absolute;
    top: 100px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    display: none; /*Hide menu when not active*/
    transition: .25s 0s ease-in-out;
  }
  
  label a {
    margin-bottom: 1em;
    display: block;
    /* color: ; */
    text-decoration: none;
    
  }

/* Fibonacci Page ------------------------------------------------------------------------ */
.fibonacci_page {
    text-align: -webkit-center;
    margin-top: 2em;
    padding: 5%;
  }
  
  /* Next and previous button for Fibonacci page ------------------------------------------- */
  .change {
    padding: 12px 24px;
    border: 0;
    border: 2px solid #333;
    box-shadow: #444440 4px 4px 0px;
    border-radius: 8px;
    color: #333;
    transition: box-shadow 200ms, -webkit-transform 200ms;
    transition: transform 200ms, box-shadow 200ms;
    transition: transform 200ms, box-shadow 200ms, -webkit-transform 200ms;
    cursor: pointer;
    -webkit-transform: translateX(15%);
            transform: translateX(15%);
  }
  
  /* when button clicked */
  .change:active {
    -webkit-transform: translateY(4px) translateX(calc(15% + 4px));
            transform: translateY(4px) translateX(calc(15% + 4px));
    box-shadow: #444440 0px 0px 0px;
  }
  
  
  /* Implementing the golden ratio or Fibonacci sequence ---------------------------------------------------------------------------*/
  .fibonacci {
    display: grid;
    height: 100%;
    grid-template-rows: repeat(89, 1fr);
    grid-template-columns: repeat(123, 1fr);
    grid-gap: 5px;
    aspect-ratio: 2/1;
    max-width: 70em;
  }
  
  .fibonacci-item {
    background: lightcoral;
    color: lightcoral;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-content: center;
    font-size: 1.5em;
    background-size: cover;
    background-position: top left;
    transition: all ease-in-out 300ms;
    position: relative;
    z-index: 10;
    border-radius: 25px;  
  }
  
  .fibonacci-item::before {
    border-radius: 25px;
  }
  
  .fibonacci-item.one {
    grid-row: 1 / span 89;
    grid-column: 1 / span 89;
  }
  .fibonacci-item.one:hover {
    color: black;
    cursor: pointer;
    background-position-x: center;
  }
  .fibonacci-item.one::before {
    content: '';
    display: block;
    background: black;
    opacity: 0.6;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: all ease-in-out 300ms;
    z-index: -1;
  }
  .fibonacci-item.one:hover::before {
    opacity: 0.2;
